<template>
  <article v-if="!isLoading">
    <section v-if="!isEmptyShow">
      <Step :step="nowStep"/>
      <router-view v-on:set-step="setStep"></router-view>
    </section>
    <section v-else class="text-center py-6">
      <h1>你的購物車是空的喔</h1>
    </section>
  </article>
</template>
<script>
import Step from "@/views/frontEnd/components/CheckStep.vue";
import AxiosClass from "@/config/axiosClass.js";
export default {
  data() {
    return {
      nowStep: 1
    };
  },
  components: {
    Step
  },
  computed: {
    isLoading() {
      return this.$store.state.isLoading;
    },
    user_email() {
      return this.$store.state.user_email;
    },
    isEmptyShow() {
      if (this.nowStep !== 3 && this.$store.state.cart.length === 0) {
        return true;
      } else {
        return false;
      }
    }
  },
  methods: {
    setStep(num) {
      this.nowStep = num;
    },
    checkCookie() {
      if (this.user_email === "") {
        this.$router.push("/login");
      } else {
        return;
      }
    }
  },
  created() {
    this.checkCookie();
  },
  watch: {
    $route(to, from) {
      this.checkCookie();
    }
  }
};
</script>